import { Avatar, Grid,Paper, Typography } from '@material-ui/core';
import React from 'react';
import styles from './Skills.module.css';
import { makeStyles } from '@material-ui/core/styles';
import react_image from './logo192.png'



const useStyles = makeStyles((theme) => ({
  
    
    large: {
      width: '90px',
      height: '90px',
      background: 'rgba(27,27,27,.91)',
   
      marginBottom:'5px',
      '@media(max-width: 780px)' : {
        width: '40px',
        height: '40px',
       
      }
     
    },
    paper: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',

        background:'none',
        color:'#4c4c4c',
        
        minWidth: '110px',
        margin: '10px',
        boxShadow:"none",
        '@media(max-width: 780px)' : {
            width: 5,
            margin: '10px auto' ,
           
           
           
          }
      },
      images:{
          width:30,
          '@media(max-width: 780px)' : {
            width: 20,
           
           
          }
      }
  }));



export const Skills = () => {
    const classes = useStyles();
    return (
        <div className={styles.skills_Wrapper_parent}>

            <div className={styles.skills_Wrapper}>
                <div className={styles.skills_heading}>
                    <h1>Skills</h1>

                  
                </div>
            
                



     
          
          

                   
        
    <div className={styles.tobeCenter}>
      <Grid container>
         
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >

          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/nodejs.png" alt="N/A"/></Avatar>
          <Typography>Node Js</Typography>

          
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/javascript--v1.png" alt="N/A"/></Avatar>
          <Typography>JavaScript</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
           <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/html-5.png" alt="N/A"/></Avatar>
           <Typography>HTML5</Typography>
           </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/css3.png" alt="N/A"/></Avatar>
          <Typography>CSS3</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
           <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/ios-filled/50/000000/stripe.png" alt="N/A"/></Avatar>
           <Typography>Stripe</Typography>
           </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://upload.wikimedia.org/wikipedia/commons/a/a7/React-icon.svg" alt="N/A" width="80%"/></Avatar>
          <Typography>React</Typography>
          </Paper>
          </Grid>
                   
                    
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/heroku.png" alt="N/A"/></Avatar>
          <Typography>Heroku</Typography>
          </Paper> 
          </Grid>                 
                    
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
           <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/redux.png" alt="N/A"/></Avatar>
           <Typography>Redux</Typography>
           </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/ios-filled/50/000000/mysql.png" alt="N/A"/></Avatar>
          <Typography>Mysql</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/50/000000/mongodb.png" alt="N/A"/></Avatar>
          <Typography>MongoDB</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/material-ui.png" alt="N/A"/></Avatar>
          <Typography>Material UI</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/git.png" alt="N/A"/></Avatar>
          <Typography>GIT</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/python--v1.png" alt="N/A"/></Avatar>
          <Typography>Python 3</Typography>
          </Paper>
          </Grid>
          <Grid xs={6} sm={4} md={3} lg={2} xl={2} >
          
                    <Paper className={classes.paper}><Avatar className={classes.large}><img className={classes.images} src="https://img.icons8.com/color/48/000000/postgreesql.png" alt="N/A"/></Avatar>
                    <Typography>Postgresql</Typography>
                    </Paper>
          </Grid>
                   
                    
                    
                    
                    
                    
     
      </Grid>
      </div>
            </div>
            

        </div>
    )
}















