import React from 'react';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import ButtonAppBar from '../AppBar/AppBarS';

import styles from './MediaQuery.module.css'
import { AnimatedSummary } from '../AnimatedSummary/AnimatedSummary';
import { ContactForm } from '../Contact/ContactForm';
import { Skills } from '../Skills/Skills';
import GitHubCalendar from 'react-github-calendar';
import { Projects } from '../Projects/Projects';
import styled from 'styled-components';

const Gapping =styled.div`
margin: 100px auto;
`

const Heading=styled.div`
// color:#c4c4c4;
color:#4c4c4c;
width:20%;
min-width: 100px;
margin:10px auto;
padding-bottom: 5px;
// border-bottom: 5px solid #c4c4c4;
border-bottom: 5px solid #4c4c4c;
`
export default function SimpleMediaQuery() {
  const small = useMediaQuery('(max-width:1024px)')
  const large =useMediaQuery('(min-width:1025px)')
  const exampleTheme = {
    background: 'transparent',
  
    // grade4: 'hsl(338, 78%, 30%)',
    // grade3: 'hsl(338, 78%, 44%)',
    // grade2: 'hsl(338, 78%, 58%)',
    // grade1: 'hsl(338, 78%, 72%)',
    grade0: 'rgba(27,27,27,.91)',
    text: '#4c4c4c',
    color: '#4c4c4c'
  };

  return small?
  <div>
      <ButtonAppBar/>
      <AnimatedSummary/>
      <Skills/>
      <Projects/>
      <ContactForm/>
      
      
     
  </div>
  :large?
  <div className={styles.mediawrapper}>
      <ButtonAppBar home={"Home"}/>
      <AnimatedSummary home={"Home"}/>
      <Skills/>
      <Projects/>
    
     <Gapping>
     <Heading><h1>Github Contribution</h1></Heading>
     <GitHubCalendar username="kamalgupta97" blockSize={15} theme={exampleTheme} fullYear={false} blockMargin={4} fontSize={16}/>
     </Gapping>
      {/* <GitHubCalendar username="grubersjoe" theme={exampleTheme}>
  <ReactTooltip delayShow={50} html />
</GitHubCalendar> */}
      <ContactForm/>
     


      
      
        
    


     
     
  </div>
  :
  <div>

  </div>
}
