import React from 'react';
import styles from './ContactForm.module.css'
import emailjs from 'emailjs-com';
import styled from 'styled-components';

const Heading=styled.div`
// color:#c4c4c4;
color:#4c4c4c;
width:15%;
min-width: 100px;
margin:10px auto;
padding-bottom: 5px;
// border-bottom: 5px solid #c4c4c4;
border-bottom: 5px solid #4c4c4c;
`

const service_id=process.env.REACT_APP_SERVICE_ID
const template_id=process.env.REACT_APP_TEMPLATE_ID
const user_id=process.env.REACT_APP_USER_ID


//emailjs.sendForm("service_s6klhzq", "template_qa8xywj", e.target, "user_l5byiBzCN2imZjgY2eWCB")

export const ContactForm = () => {

    // console.log(service_id,template_id,user_id)
    const handleSubmit =e=>{
        e.preventDefault()// Prevents default refresh by the browser
    emailjs.sendForm(service_id, template_id, e.target, user_id)
    .then(result => {
    alert('Message Sent, I\'ll get back to you shortly', result.text);
    },
    error => {
    alert( 'An error occured, Plese try again',error.text)
    })
    }
    return (
        <div className={styles.Maincont}>
            <Heading><h1>Connect Me</h1></Heading>
        <form className={styles.contactForm} onSubmit={handleSubmit}>
            <div><label htmlFor="">Name:</label></div>
            <input type="text" placeholder="Name" name="from_name"/>
            <div><label htmlFor="" >Email:</label></div>
            <input type="Email" placeholder="Email" name="from_email"/>
            <div><label htmlFor="" >Message:</label></div>
            <textarea placeholder="message" name="message"/>

           <div> <button className={styles.form_submit}>Submit</button></div>
          
        </form>
        </div>
    )
}
